@import './variables.scss';

html
{
    background: $color-white-dark;
    width: 100%;
    overflow-x: hidden;
}

html, body 
{
    height: 100%;
}

// --------------------------------------------------- HEADER
.header
{
    position: relative;
    z-index: 1;
    border-bottom: 3px solid $color-orange;
    background: $color-white;
    
    &:after
    {
        content:'';
        display: block;
        position: absolute;
        bottom: -3px;
        left: 0;
        right:80%;
        height: 3px;
        background: $color-blue-dark; 
    }

    .logo
    { 
        width: 100%;
        height: distance(3);
        text-align: center;
        margin-top: distance(.25);
        margin-left: distance(1.5);

        @media screen and (min-width: 640px) {
            float: left;
            width: auto;
        }

        @media screen and (max-width: 640px) {
            padding: distance() 0;
            margin:  0;
            height: distance(5);
        }
    }

    .module-social
    {
        float:right;
    }
}

    .header-content
    {
        margin: 0 auto;
        max-width: $site-width;
        width: 100%;
    }

// --------------------------------------------------- BODY
.body
{
    position: relative;
    padding: distance(4) 0 distance(2);
    z-index: 0;
    min-height: 100%;
    padding-top: distance(8);
    padding-bottom: distance(8);
    margin-top: distance(-6) - 3px;
    margin-bottom: distance(-6);
}
    .body-background
    {
        display: block;
        position: fixed;
        z-index: -1;
        left: 0;
        right: 0;
        top: distance(0);
        transform: translateZ(0) translateX(0);

        img {
            display: block;
        }
    }

    .body-content
    {
        margin: 0 auto;
        max-width: $site-width;
        width: 100%;
    }
    
    // Frame
    .frame 
    {
        padding: distance(1.5);
    }

    .frame-title
    {
        float: left;
        position: relative;
        padding: 0 distance();
        line-height: distance(2);
        font-size: 15px;
        font-style: italic;
        background: $color-blue-dark;
        color: $color-white;

        &:after
        {
            content:"";
            display: block;
            position: absolute;
            left: 100%;
            bottom: 0;
            height: 0;
            width: 0;
            border: distance() solid transparent;
            border-bottom-color: $color-blue-dark;
            border-left-color: $color-blue-dark;
        }

        .icon
        {
            color: $color-orange;
            margin-right: distance(.25);
        }

        &.is-orange
        {
            color: $color-blue-dark;
            background: $color-orange;
            font-weight: bold;
            .icon { color: $color-blue-dark; }
            &:after { 
                border-bottom-color: $color-orange;
                border-left-color: $color-orange;
            }
        }
    }

    .frame-image
    {
        box-shadow: 0 0 20px darken($color-blue-dark, 1.2);
        img 
        {
            display: block;
        }
    }

    .frame-content
    {
        clear: both;
        position: relative;
        background: $color-white;
        border-bottom: 1px solid $color-orange;
        box-shadow: 0 0 6px rgba(black, 0.1);
        z-index: 1;

        &:after
        {
            content:"";
            display: block;
            position: absolute;
            bottom: -1px;
            left: 0;
            right: 80%;
            height: 1px;
            background: $color-blue-dark;
        }
    }

// --------------------------------------------------- FOOTER
.footer
{
    position: relative;
    z-index: 1;
    text-align: right;
    color: $color-white;
    background: $color-blue-dark;
    border-bottom: distance() solid $color-orange;
    padding-top: distance();

    .module-copyright {
        font-size: 10px;
        padding-right: distance();
        line-height: distance(2);
    }
}

    .footer-content
    {
        margin: 0 auto;
        max-width: $site-width;
        width: 100%;
    }



// --------------------------------------------------- IMAGES
img {
    width: auto;
    height: auto;
    max-width: 100%;
    max-height: 100%;
}